.moreinfo-container {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: -100vh;
    -webkit-transition: all 200ms ease-in-out;
    -o-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
    z-index: 100;
    padding: 0;
    margin: 0;
}

.moreinfo-container .close-button {
    position: absolute;
    display: inline-block;
    top: 25px;
    right: 25px;
    font-size: 2em;
    cursor: pointer;
    color: white;
}

.moreinfo-container.show {
    top: 0;
}

.moreinfo-container .moreinfo-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    color: white;
    text-align: center;
    font-size: 1.6em;
}

.moreinfo-container .moreinfo-content a {
    color: white;
}

.moreinfo-container .background {
    background: black;
    opacity: 0.8;
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.moreinfo-container .moreinfo-content span{
    display: block;
}


